@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  background-image: url(/build/img/bg-intro-desktop.png);
  background-color: rgb(255, 122, 122);
  background-size: cover;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

p {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
}

@media (min-width: 1165px) {
  h1 {
    font-size: 5rem;
  }
}
.contenedor {
  width: 85%;
  margin: auto;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
.contenedor .contenedor-titulo {
  width: 50rem;
}
.contenedor .contenedor-titulo h1 {
  font-family: "Poppins", sans-serif;
  color: white;
}
.contenedor .contenedor-titulo p {
  color: white;
}
.contenedor .contenedor-formulario {
  width: 55rem;
}

@media (max-width: 1165px) {
  .contenedor {
    margin-bottom: 5rem;
    height: auto;
  }
  .contenedor-titulo {
    text-align: center;
  }
  h1 {
    font-size: 5rem;
    text-align: center;
  }
}
@media (max-width: 467px) {
  .contenedor {
    margin-top: 5rem;
  }
  h1 {
    font-size: 3rem;
  }
  .contenedor-titulo p {
    margin-bottom: 5rem;
  }
}
.free {
  background-color: rgb(96, 85, 165);
  text-align: center;
  color: white;
  border-radius: 1rem;
  margin-bottom: 3rem;
  box-shadow: 0rem 0.8rem rgba(0, 0, 0, 0.2);
}
.free span {
  font-weight: 700;
}

.formulario {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 1rem;
  box-shadow: 0px 7px 0px rgba(0, 0, 0, 0.1);
}
.formulario .error {
  height: 72px;
}
.formulario input {
  width: 100%;
  border-radius: 0.5rem;
  border-width: 1px;
  padding: 1.5rem 2.5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  border-color: rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem;
}
.formulario input:focus {
  border: 1.7px solid rgb(96, 85, 165);
  outline: none;
  caret-color: rgb(96, 85, 165);
  box-shadow: 0rem 0.3px black;
}
.formulario button {
  background-color: rgb(56, 204, 140);
  border: 1px solid rgba(40, 148, 101, 0.5);
  padding: 2rem;
  text-transform: uppercase;
  color: white;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  font-weight: 700;
  box-shadow: 0 2px 0px #2a9b6a;
}
.formulario p {
  margin-top: 1.8rem;
  font-size: 1.2rem;
  text-align: center;
  color: rgb(185, 182, 211);
  font-weight: 500;
}
.formulario p span {
  color: rgb(255, 122, 122);
  font-weight: 700;
  cursor: pointer;
}

@media (min-width: 1165px) {
  .free {
    padding: 0.1rem 1rem;
  }
  .formulario {
    padding: 4rem;
  }
}
@media (max-width: 1165px) {
  .free {
    padding: 0.1rem 1rem;
  }
  .formulario {
    padding: 4rem;
  }
}
@media (max-width: 467px) {
  .free {
    padding: 0.5rem 5rem;
  }
  .formulario {
    padding: 2rem;
  }
}
.activo {
  position: relative;
  margin-bottom: 20px;
}
.activo input {
  border: 2px solid rgb(255, 122, 122);
}
.activo input::placeholder {
  all: initial;
  font-size: 0;
}
.activo .error-imagen {
  position: absolute;
  background-image: url(/build/img/icon-error.svg);
  height: 24px;
  width: 24px;
  right: 30px;
  top: 15px;
}
.activo .error-texto {
  position: absolute;
  right: 0;
  top: 40px;
  color: rgb(255, 122, 122);
  font-style: italic;
}